import { ChakraProvider, createSystem, defineConfig, defaultConfig } from "@chakra-ui/react";
import { AppProps } from "next/app";

// 1. Define your custom configuration
const config = defineConfig({
  theme: {
    tokens: {
      colors: {
        brand: {
          50: { value: "#e6fffa" },
          500: { value: "#319795" },
          900: { value: "#171923" },
        },
      },
      fonts: {
        heading: { value: `'Inter', sans-serif` },
        body: { value: `'Inter', sans-serif` },
      },
    },
  },
});

// 2. Create the system (this replaces extendTheme)
const system = createSystem(defaultConfig, config);

function MyApp({ Component, pageProps }: AppProps) {
  return (
    // In v3, the prop is 'value' instead of 'theme'
    <ChakraProvider value={system}>
      <Component {...pageProps} />
    </ChakraProvider>
  );
}

export default MyApp;